<template>
  <div id="currency">
    <head-vue title="Currency" goBack="true">
      <div slot="setRight" @click="save">Save</div>
    </head-vue>
    <ul>
      <li v-for="item in currencyArr" :key="item.id">
        <span>{{item.curr}}</span>
        <div class="li_right" @click="checkCurrency(item)">
          <img src="../../assets/me/defunct@3x.png" alt="" v-if="!item.checked">
          <img src="../../assets/me/click@3x.png" alt="" v-if="item.checked">
        </div>
      </li>
    </ul>
  </div>
</template>
<script>
import head from '../../components/common/head'
import { getStore, setStore } from '../../utils'

import {
// settingMonetarytUnitRequest
} from '../../api/method'
import {
// settingMonetarytUnitResponse
} from '../../api/getData'
import { mapActions } from 'vuex'
export default {
  data () {
    return {
      currencyArr: [
        {
          curr: 'GBP',
          type: 2,
          index: 0,
          checked: true
        },
        {
          curr: 'USD',
          type: 3,
          index: 1,
          checked: false
        },
        {
          curr: 'JPY',
          type: 5,
          index: 2,
          checked: false
        },
        {
          curr: 'EUR',
          type: 4,
          index: 3,
          checked: false
        }
      ],
      currIndex: 0
    }
  },
  components: {
    'head-vue': head
  },
  computed: {
  },
  created () {
    // settingMonetarytUnitResponse(this.NAVIGATOR, this)
  },
  methods: {
    ...mapActions(['setCurrency']),
    checkCurrency (item) {
      this.currencyArr.forEach(element => {
        element.checked = false
      })
      item.checked = !item.checked
      this.currIndex = item.index
    },
    save () {
      // settingMonetarytUnitRequest(this.NAVIGATOR, this, this.currencyArr[this.currIndex].type)
      console.log(this.currencyArr[this.currIndex].type)
      this.setCurrency(this.currencyArr[this.currIndex].curr)
      let loginMessage = JSON.parse(getStore('login'))
      let Rate = {
        email: loginMessage.email,
        currency: this.currencyArr[this.currIndex].curr
      }
      let localRateCurrency = JSON.parse(getStore('localRateCurrency'))
      if (localRateCurrency) {
        let flag = true
        localRateCurrency.forEach(element => {
          if (Rate.email === element.email) {
            element.currency = Rate.currency
            flag = false
          }
        })
        if (flag) {
          localRateCurrency.push(Rate)
        }
        setStore('localRateCurrency', localRateCurrency)
      } else {
        let localRateCurrency = [Rate]
        setStore('localRateCurrency', localRateCurrency)
      }
      // if (this.$root.ISDEBUG) {
      //   window.settingMonetarytUnitResponse('3123123', true)
      // }
      this.$router.go(-1)
    }
  }
}
</script>
<style lang="less" scoped>
#currency{
  .fs;
  z-index: 999;
  background: white;
  ul{
    .wh(100%, auto);
    display: flex;
    flex-direction: column;
    li{
      display: flex;
      justify-content: space-between;
      .wh-l(100%,100px);
      padding: 0 30px;
      border-bottom: 1px solid #cccccc;
      span{
        color: #585858;
        font-weight: 400;
        font-size: 28px;
      }
      .li_right{
        .wh(40px,40px);
        align-items: center;
        img{
          .wh(100%, 100%)
        }
      }
    }
  }
  img{
    display: inline-block;
  }
}
</style>



